import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'HomeView',
    component: HomeView,
    children: [
      {
        path: '/onlineVideo',
        name: '在线影视',
        component: () => import('@/views/video/OnlineVideo.vue')
      }, {
        path: '/onlineResources',
        name: '在线资源',
        component: () => import('@/views/resources/OnlineResources.vue')
      }, {
        path: '/quickResource',
        name: '快捷资源',
        component: () => import('@/views/resources/QuickResource.vue')
      }, {
        path: '/myHomePage',
        name: '我的主页',
        component: () => import('@/views/about/MyHomePage.vue')
      },

    ]
  },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
