<template>
  <div class="home">
    <el-backtop/>
    <el-container>
      <el-header class="homeHeader">
        <div class="title">
          {{ systemInfo.name }} {{ systemInfo.version }}
        </div>

        <div class="textArr">
          <p class="slice-enter-active" :style="{color:text.color}" :key="text.id">
            {{ text.value }}</p>
        </div>

        <div>
          <!--占位-->
        </div>

      </el-header>

      <el-container>

        <el-aside width="168">
          <div align="center">
            <el-radio-group v-model="isCollapse" style="margin-top: 20px;margin-bottom: 10px;width: 100%">
              <el-radio-button :label="!isCollapse" title="展示菜单">
                <i :class="isCollapse?'el-icon-d-arrow-right':'el-icon-d-arrow-left'"></i></el-radio-button>
            </el-radio-group>
          </div>
          <el-menu
              :collapse="isCollapse"
              :unique-opened="true"
              :default-active="menuDefaultActive"
              class="el-menu-vertical-demo">

            <el-submenu v-for="(menu1,index1) in menus" :key="index1" :index="menu1.index"
                        :disabled="menu1.disabled">
              <template #title>
                <el-icon :class="menu1.icon"></el-icon>
                <span>{{ menu1.name }}</span>
              </template>
              <el-menu-item v-for="(menu2,index2) in menu1.children" :key="index2"
                            :index="menu2.index" @click="goPath(menu2.url)"
                            :disabled="menu2.disabled">
                <el-icon :class="menu2.icon"></el-icon>
                {{ menu2.name }}
              </el-menu-item>
            </el-submenu>
          </el-menu>

        </el-aside>
        <el-main>
          <router-view/>
        </el-main>
      </el-container>
    </el-container>

  </div>
</template>

<script setup>

import {ref, reactive, computed, onMounted} from "vue";
import router from "@/router";

//菜单栏收缩开关
const isCollapse = ref(true);
//菜单列表
const menus = ref({});
//默认展示页面
const menuDefaultActive = ref(null);
//系统信息
const systemInfo = ref({});
//滚动公告
const scroll = reactive({
  number: 0,
  textArr: [{
    "value": "",
    "color": ""
  }]
})


onMounted(() => {
  initVersionInfo();
  initMenus();
  initScrollTextArr();
  startMove();
})

const text = computed(() => {
      return {
        id: scroll.number,
        value: scroll.textArr[scroll.number].value,
        color: scroll.textArr[scroll.number].color,
      }
    }
)

//初始化公告列表
function initScrollTextArr() {
  scroll.textArr.push({
    value: '云上游乐园 2.0上线',
    color: '#' + (Math.random() * 0xffffff << 0).toString(16)
  })
}

//初始化菜单列表
function initMenus() {
  const resourceData = require('./homeView.json');
  menus.value = resourceData;
  defaultPage(resourceData);
}

//默认展开资源
function defaultPage(resourceData) {
  function getPage(val) {
    if (val[0].children) return getPage(val[0].children);
    else return val[0];
  }

  let rs = getPage(resourceData);
  menuDefaultActive.value = rs.index;
  goPath(rs.url);
}

//前往页面
function goPath(path) {
  router.push(path);
}

//初始化系统信息
function initVersionInfo() {
  systemInfo.value.name = '云上游乐园'
  systemInfo.value.version = '2.0'
}

function startMove() {
  // eslint-disable-next-line
  let timer = setInterval(() => {
    if (scroll.number === scroll.textArr.length - 1) {
      scroll.number = 0;
    } else {
      scroll.number += 1;
    }
  }, 5000); // 滚动不需要停顿则将2000改成动画持续时间
}


</script>
